import styles from "./TotalOrderInfor.module.css";
import { useSelector } from "react-redux";
import { useFormatPrice } from "./customHooks/useFormatPrice";
import OrderPriceItem from "./OrderPriceItem";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { cartActions } from "../redux/store";
import { useNavigate } from "react-router-dom";
const TotalOrderInfor = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const totalPrice = useFormatPrice(
    useSelector((state) => state.cart.totalPrice) ?? 0
  );
  //Khai bo list cart c filter theo email ca user ang ng nhp
  const listCart = useSelector((state) => state.cart.listCart);
  const getListCart = async () => {
    const response = await fetch(
      `${process.env.REACT_APP_BACKEND_URL}/user/listcart`,
      {
        method: "GET",
        credentials: "include",
      }
    );
    if (response.ok) {
      const data = await response.json();
      dispatch(cartActions.UPDATECART(data.cart));
      //Nu cha ng nhp khng th dng chc nng cart phi ng nhp trc
    } else {
      if (response.status === 401) {
        navigate("/login?mode=login");
      } else if (response.status === 500) {
        navigate("/servererror");
      }
    }
  };
  //Nu nh l f5 hay load li trang checkout th phi check coi  fetch data cart cha nu cha s fetch li
  useEffect(() => {
    if (!totalPrice) {
      getListCart();
    }
  }, []);

  //component tr v JSX  hin th thng tin cui cng ca n hng, trong  mi OrderpriceItem s hin th mi dng l 1 sn phm
  return (
    <div className={styles.container}>
      <h3>YOUR ORDER</h3>
      <ul>
        {listCart.map((item) => (
          <OrderPriceItem key={item._id} item={item}></OrderPriceItem>
        ))}
      </ul>
      <h4 className={styles.finalPrice}>
        <span>TOTAL</span>
        <span className={styles.totalprice}> {totalPrice}VND</span>
      </h4>
    </div>
  );
};

export default TotalOrderInfor;
