import React, { useEffect } from "react";
import { useState } from "react";
import styles from "./CartItem.module.css";
import { cartActions } from "../redux/store";
import { useDispatch } from "react-redux";
import { useFormatPrice } from "./customHooks/useFormatPrice";
import { useNavigate } from "react-router-dom";

const CartItem = (props) => {
  //Khai bo cc bin thng tin
  const [quantity, setQuantity] = useState(props.productCart.quantity);
  const navigate = useNavigate();
  //dng custom hook  nh dng phn price cho price n v price tng
  const productPrice = useFormatPrice(props.productCart.productId.price);
  const productQuantityxPrice = useFormatPrice(
    props.productCart.productId.price * quantity
  );

  //Xy dng function  fetch api  update cart  database
  const postUpdateCart = async (action) => {
    const idUpdateCart = props.productCart.productId._id;
    const response = await fetch(
      `${process.env.REACT_APP_BACKEND_URL}/user/updatecart`,
      {
        method: "POST",
        mode: "cors",
        credentials: "include",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ action: action, productId: idUpdateCart }),
      }
    );
    if (response.ok) {
      props.getListCart();
      const data = await response.json();
      setQuantity(data.quantity);
    } else {
      if (response.status === 401) {
        navigate("/login?mode=login");
      } else if (response.status === 500) {
        navigate("/servererror");
      }
    }
  };
  //Xy dng function tng s lng  tng cng ging remove nhng vi action gim s lng, khng cho b hn 1
  const decreaseQuant = () => {
    postUpdateCart("decrease");
  };
  //Xy dng function tng s lng  tng cng ging remove nhng vi action tng s lng
  const increaseQuant = () => {
    postUpdateCart("increase");
  };
  //Xy dng function  xa sn phm, lc listCart ch gi li nhng product c id khc vi id sn phm b click vo nt remove hoc khc email ang ng nhp
  const removeProductHandler = async () => {
    const idCartDelete = props.productCart.productId._id;
    const response = await fetch(
      `${process.env.REACT_APP_BACKEND_URL}/user/deletecart`,
      {
        method: "POST",
        mode: "cors",
        credentials: "include",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ productId: idCartDelete }),
      }
    );
    if (response.ok) {
      props.getListCart();
    } else {
      if (response.status === 401) {
        navigate("/login?mode=login");
      } else if (response.status === 500) {
        navigate("/servererror");
      }
    }
  };
  //JSX tr ra ra tng dng trong table cha cc thng tin tng ng v cc sn phm c tng user thm vo
  return (
    <tr
      key={props.productCart.id}
      id={props.productCart.id}
      className="cartRow"
    >
      <td className={styles.imageProduct}>
        <img
          src={
            props.productCart.productId.img1.includes("https://firebasestorage")
              ? props.productCart.productId.img1
              : process.env.REACT_APP_BACKEND_URL +
                props.productCart.productId.img1
          }
        ></img>
      </td>
      <td className={styles.nameProduct}>{props.productCart.productId.name}</td>
      <td className={styles.priceProduct}>
        {productPrice} <br /> VND
      </td>
      <td className={styles.quantityProduct}>
        <i className="fa-solid fa-caret-left" onClick={decreaseQuant}></i>
        {quantity}
        <i className="fa-solid fa-caret-right" onClick={increaseQuant}></i>
      </td>
      <td className={styles.priceProduct}>
        {productQuantityxPrice} <br />
        VND
      </td>
      <td className={styles.removeProduct}>
        <i
          className="fa-regular fa-trash-can"
          onClick={removeProductHandler}
        ></i>
      </td>
    </tr>
  );
};
export default React.memo(CartItem);
