import { useEffect, useState } from "react";
import styles from "./UserForm.module.css";
import {
  Link,
  useSearchParams,
  useActionData,
  useNavigate,
} from "react-router-dom";
import { loginActions } from "../redux/store";
import { useDispatch } from "react-redux";
const UserForm = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  //khai bo bin cha cc error, nu ban u cha c li no th s l object rng
  const [dataError, setDataError] = useState({});
  //Khai bo hook  ly param URL v ly gi tr mode, nu mode l login th bin isLogin l true
  const [searchParams] = useSearchParams();
  const isLogin = searchParams.get("mode") === "login";
  //Khai bo cc bin dng state qun l cho cc input Name, Email, Password, Phone v cc bin c li hay khng ca tng loi tng ng
  const [enterName, setEnterName] = useState("");
  const [nameIsError, setNameIsError] = useState(false);
  const [enterEmail, setEnterEmail] = useState("");
  const [emailIsError, setEmailIsError] = useState(false);
  const [enterPassword, setEnterPassword] = useState("");
  const [passwordIsError, setpasswordIsError] = useState(false);
  const [enterPhone, setEnterPhone] = useState("");
  const [phoneIsError, setPhoneIsError] = useState(false);
  //Khai bo bin dng state  qun l c ang chuyn gia 2 mode hay khng
  const [isChangeMode, setIsChangeMode] = useState(false);
  // Cc function  qun l onChange ca cc input
  //Nu nh ang c li m user input li th bin qun l li ca input  s thnh true  bin mt cc classstyle li v xa ni dung ca li trong dataError
  const passwordInputHandler = (event) => {
    setEnterPassword(event.target.value);
    if (event.target.value.length >= 8) {
      setpasswordIsError((prev) => false);
    }
  };
  const changeNameHandler = (event) => {
    setEnterName(event.target.value);
    if (event.target.value.length >= 1) {
      setNameIsError((prev) => false);
    }
  };
  const changeEmailHandler = (event) => {
    setEnterEmail(event.target.value);
    if (event.target.value.length >= 1) {
      setEmailIsError(false);
    }
  };
  const changePhoneHandler = (event) => {
    setEnterPhone(event.target.value);
    if (event.target.value.length >= 1) {
      setPhoneIsError(false);
    }
  };
  // S dng useEffect nu nh c data ca li tng ng trong dataError th set bin qun l li thnh true tc l ang c li
  useEffect(() => {
    if (dataError.password) {
      setpasswordIsError((prev) => true);
    }
    if (dataError.name) {
      setNameIsError((prev) => true);
    }
    if (dataError.email) {
      setEmailIsError((prev) => true);
    }
    if (dataError.phone) {
      setPhoneIsError((prev) => true);
    }
  }, [dataError]);

  // Khi submit form  ch  login th s xa passWord
  const submitFormHandler = (event) => {
    event.preventDefault();
    const urlToFetch = `${process.env.REACT_APP_BACKEND_URL}/user/${
      isLogin ? "login" : "signup"
    }`;
    //function  post data n backend ng nhp hoc ng k
    const postUserAccessData = async () => {
      let dataToFetch = {
        name: enterName,
        email: enterEmail,
        password: enterPassword,
        phone: enterPhone,
      };
      if (isLogin) {
        dataToFetch = {
          email: enterEmail,
          password: enterPassword,
        };
      }
      const response = await fetch(urlToFetch, {
        method: "POST",
        mode: "cors",
        credentials: "include",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(dataToFetch),
      });
      if (response.ok) {
        if (isLogin) {
          const userData = await response.json();
          console.log(userData);
          dispatch(loginActions.ON_LOGIN(userData));
          navigate("/");
        } else {
          navigate("/login?mode=login");
        }
      } else {
        if (response.status === 400) {
          const errors = await response.json();
          const msgError = {};
          for (let property in errors) {
            msgError[property] = errors[property].msg;
          }
          setDataError(msgError);
        } else if (response.status === 401 && isLogin) {
          const error = await response.json();
          setDataError({ password: error.message });
        } else if (response.status === 500) {
          navigate("/servererror");
        }
      }
    };
    postUserAccessData();
    if (isLogin) {
      setEnterPassword("");
    }
  };
  //to hm xa data v cc li ca cc input khi click thay i mode gia login v sign up, set bin hin th ...Loading
  const changeModeHanlder = () => {
    setIsChangeMode(true);
  };
  useEffect(() => {
    setEnterPassword("");
    setEnterName("");
    setEnterEmail("");
    setEnterPhone("");
    setEmailIsError((prev) => false);
    setpasswordIsError((prev) => false);
    setNameIsError((prev) => false);
    setPhoneIsError((prev) => false);
    setIsChangeMode(false);
  }, [isLogin]);
  //Khi click change gia mode login v signup s hin th message ...Loading
  //component return JSX xy dng form data cho c 2 ch  login v signup render ph thuc vo mode ly t params
  return (
    <div className={styles.container}>
      <div className={styles.formContainer}>
        {isChangeMode ? (
          <p className={styles.loadingMessage}>...Loading</p>
        ) : (
          <form
            onSubmit={submitFormHandler}
            method="post"
            className={styles.form}
          >
            <h2>{isLogin ? "Sign In" : "Sign Up"}</h2>
            {!isLogin && (
              <input
                placeholder="Full Name"
                type="text"
                id="name"
                name="name"
                value={enterName}
                onChange={changeNameHandler}
                className={nameIsError ? styles.invalidInput : ""}
              ></input>
            )}
            {nameIsError && (
              <p className={styles.listError}>{dataError.name}</p>
            )}
            <input
              placeholder="Email"
              type="email"
              id="email"
              name="email"
              value={enterEmail}
              onChange={changeEmailHandler}
              className={emailIsError ? styles.invalidInput : ""}
            ></input>
            {dataError.email && emailIsError && (
              <p className={styles.listError}>{dataError.email}</p>
            )}
            <input
              placeholder="Password"
              type="password"
              id="password"
              name="password"
              onChange={passwordInputHandler}
              value={enterPassword}
              className={passwordIsError ? styles.invalidInput : ""}
            ></input>
            {dataError.password && passwordIsError && (
              <p className={styles.listError}>{dataError.password}</p>
            )}
            {!isLogin && (
              <input
                placeholder="Phone"
                type="number"
                id="phone"
                name="phone"
                value={enterPhone}
                onChange={changePhoneHandler}
                className={phoneIsError ? styles.invalidInput : ""}
              ></input>
            )}
            {dataError.phone && phoneIsError && (
              <p className={styles.listError}>{dataError.phone}</p>
            )}

            <button>{isLogin ? "SIGN IN" : "SIGN UP"}</button>
            <span>
              {isLogin ? "Create an account? " : "Login? "}
              <Link
                to={`/login?mode=${isLogin ? "signup" : "login"}`}
                onClick={changeModeHanlder}
              >
                {isLogin ? "Sign Up" : "Click"}
              </Link>
            </span>
          </form>
        )}
      </div>
    </div>
  );
};
export default UserForm;
