import React from "react";
import "./Popup.module.css";
import ReactDOM from "react-dom";
import styles from "./Popup.module.css";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { popupActions } from "../redux/store";
import { useNavigate } from "react-router-dom";
import Fade from "./Transitions/Fade";
import { useFormatPrice } from "./customHooks/useFormatPrice";
// Xy dng backdrop vi lp ph mu en cng vi animation <Fade>
const Backdrop = () => {
  return (
    <Fade>
      <div className={styles.backdrop}></div>
    </Fade>
  );
};
//Xy dng Popup Modal vi thng tin ca sn phm c click vo
const Modal = (props) => {
  const dispatch = useDispatch();
  //s dng custom hook useFormatPrice  format price ng nh dng
  let priceOrigi = "";
  if (props.dataProduct) {
    priceOrigi = props.dataProduct.price;
  }
  let price = useFormatPrice(priceOrigi);
  const navigate = useNavigate();
  //Dispatch action showPopUp t true => false  ng popup
  const exitPopupHandler = () => {
    dispatch(popupActions.showPopup());
    dispatch(popupActions.getDataPopup(""));
  };
  //Khi n vo view detail user s c chuyn n trang chi tit ca sn phm tng ng
  const navigatetoProductHandler = () => {
    dispatch(popupActions.showPopup());
    dispatch(popupActions.getDataPopup(""));
    navigate(
      `/detail/${props.dataProduct._id}?category=${props.dataProduct.category}`
    );
  };

  return (
    <Fade>
      {props.dataProduct && (
        <div className={styles.modalPopup}>
          <img
            src={
              props.dataProduct.img1.includes("https://firebasestorage")
                ? props.dataProduct.img1
                : process.env.REACT_APP_BACKEND_URL + props.dataProduct.img1
            }
          />
          <div className={styles.popupContent}>
            <h3>{props.dataProduct.name}</h3>
            <h4>{price} VND</h4>
            <p>{props.dataProduct.short_desc}</p>
            <button onClick={navigatetoProductHandler}>
              <i className="fa-solid fa-cart-shopping"></i> View Detail
            </button>
          </div>
          <i className="fa-solid fa-xmark" onClick={exitPopupHandler}></i>
        </div>
      )}
    </Fade>
  );
};
//Component chnh l Popup c xy dng bng react portal ca 2 thnh phn con l backdrop modal
const Popup = (props) => {
  return (
    <>
      {ReactDOM.createPortal(
        <Backdrop />,
        document.getElementById("backdrop-root")
      )}
      {ReactDOM.createPortal(
        <Modal dataProduct={props.dataProduct[0]} />,
        document.getElementById("overlay-root")
      )}
    </>
  );
};
export default Popup;
