import React from "react";
import { useState, useEffect } from "react";
//Xy dng custom hook  chuyn nh dng Price c s dng  nhiu component trong page
export const useFormatPrice = (originalPrice) => {
  let [price, setPrice] = useState("");
  //S dng useeffect  thc hin hm chuyn i format price khi gi tr price c truyn xung
  // chuyn tng char trong chui price vo string nu  l k t th 3 th s thm du phy  ng format
  useEffect(() => {
    let priceTemp = "";
    let priceString = originalPrice.toString();
    for (let i = priceString.length - 1; i >= 0; i--) {
      priceTemp = priceString[i] + priceTemp;
      //c 3 k t th s thm 1 du chm ngn cch v  khng phi l gi tr th i ng u trong dy k t
      if ((priceString.length - i) % 3 === 0 && i !== 0) {
        priceTemp = "." + priceTemp;
      }
    }
    setPrice(priceTemp);
  }, [originalPrice]);
  return price;
};
