import { createSlice, configureStore } from "@reduxjs/toolkit";

//Initial state v slice cho phn popUp  trang ch, lu tr Id sn phm c click vo v bin qun l show hay hide popUp
const initialStatePopup = {
  popUpProductId: "",
  popUpShow: false,
};
const popUpSlice = createSlice({
  name: "popup",
  initialState: initialStatePopup,
  reducers: {
    showPopup: (state, action) => {
      state.popUpShow = !state.popUpShow;
    },
    getDataPopup: (state, action) => {
      state.popUpProductId = action.payload;
    },
  },
});
//Initial state v slice cho phn filter sn phm theo category  trang shop, qun l category  trang shop, category mc nh l "All"
const initialStateCategory = {
  category: "All",
};
const selectCategorySlice = createSlice({
  name: "category",
  initialState: initialStateCategory,
  reducers: {
    setCategory: (state, action) => {
      state.category = action.payload;
    },
  },
});

//Initial state v slice cho phn login v signup, nu bin currUser ly t localstage l mng rng th l ngi dng cha ng nhp
// Lu thng tin ngi dng hin ti bng data ly t mng currUser ca localstorage
const initialStateLogin = {
  isLogin: false,
  curUser: null,
};
const loginSlice = createSlice({
  name: "login",
  initialState: initialStateLogin,
  reducers: {
    //set trng thi ng nhp v thng tin ngi dng ang ng nhp
    ON_LOGIN: (state, action) => {
      state.isLogin = true;
      state.curUser = action.payload;
    },
    //Logout th set mng curUser thnh mng rng
    ON_LOGOUT: (state, action) => {
      state.isLogin = false;
      state.curUser = null;
    },
  },
});
//Initial state v slice cho phn cart cha 1 key lu danh sch list sn phm c trong cart v 1 bin tnh tng gi tin
const initialStateCart = {
  listCart: [],
  totalPrice: 0,
};
const cartSlice = createSlice({
  name: "cart",
  initialState: initialStateCart,
  reducers: {
    //Update li gi tng
    UPDATECART: (state, action) => {
      state.listCart = action.payload;
      state.totalPrice = state.listCart.reduce((acc, cur) => {
        return acc + cur.quantity * cur.productId.price;
      }, 0);
    },
  },
});
//Bin cha data lu tr store bi cc reducer ca cc slice
const store = configureStore({
  reducer: {
    popUp: popUpSlice.reducer,
    category: selectCategorySlice.reducer,
    login: loginSlice.reducer,
    cart: cartSlice.reducer,
  },
});
//Export cc action t cc slice  d s dng
export const popupActions = popUpSlice.actions;
export const categoryActions = selectCategorySlice.actions;
export const loginActions = loginSlice.actions;
export const cartActions = cartSlice.actions;
export default store;
