import { useNavigate, useLocation } from "react-router-dom";
import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import styles from "./MainNavigation.module.css";
import { loginActions } from "../redux/store";
import "@fortawesome/fontawesome-free/css/all.min.css";
//Theo yu cu  bi phi dng useNavigate nn em s dng useNavigate thay g dng NavLink
const MainNavigation = function (props) {
  //Khai bo cc hook  s dng
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const location = useLocation();
  //Khai bo bin c ang ng nhp hay khng ly d liu t redux store
  const isLogin = useSelector((state) => state.login.isLogin);
  //Khai bo bin current user data t redux store
  const curUserData = useSelector((state) => state.login.curUser);
  //Khai bo bin lu Url hin ti
  const [curUrl, setCurUrl] = useState(null);
  //Cc function  iu hng n cc trang tng ng khi n vo link ca tng route trn navbar
  const landToHomeHandler = () => {
    navigate("/");
  };
  const landToShopHandler = () => {
    navigate("/shop");
  };
  const landToCartHandler = () => {
    navigate("/cart");
  };
  const landToLoginHandler = () => {
    navigate("/login?mode=login");
  };
  const landToHistoryHandler = () => {
    navigate("/history");
  };
  // Function  thc hin hnh ng logout khi click vo nut logout
  const logOutHandler = async () => {
    //Dispatch action logout, xa currentUser khi localstorage v iu hng v trang login
    const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/user/userlogout`, {
      credentials: "include",
    });
    console.log(response);
    if (response.ok) {
      dispatch(loginActions.ON_LOGOUT());
      navigate("/login?mode=login");
    } else {
      if (response.status === 401) {
        navigate("/login?mode=login");
      } else if (response.status === 500) {
        navigate("/servererror");
      }
    }
  };
  //Function  thc hin hnh ng click vo Narbar truyn t rootlayout xung
  const clickOnNavigationHandler = () => {
    props.clickOnNavHandler();
  };
  // s dng useffect  cp nht state qun l URL mi ln ng link thay i
  useEffect(() => {
    setCurUrl(location.pathname);
  }, [location]);
  //JSX tr ra thanh navbar nu current URL bng vi ng link ca route no th phn t navbar  s nhn class style active
  return (
    <nav className={styles.navBarContainer} onClick={clickOnNavigationHandler}>
      <ul>
        <li
          onClick={landToHomeHandler}
          className={curUrl === "/" ? styles.active : undefined}
        >
          Home
        </li>
        <li
          onClick={landToShopHandler}
          className={curUrl === "/shop" ? styles.active : undefined}
        >
          Shop
        </li>
      </ul>
      <h2>BOUTIQUE</h2>
      <ul>
        <li
          onClick={landToCartHandler}
          className={curUrl === "/cart" ? styles.active : undefined}
        >
          <i className="fa-solid fa-cart-flatbed"></i>
          Cart
        </li>
        {!isLogin && (
          <li
            onClick={landToLoginHandler}
            className={curUrl === "/login" ? styles.active : undefined}
          >
            <i className="fa-solid fa-user-large"></i> Login
          </li>
        )}
        {isLogin && (
          <li onClick={landToHistoryHandler}>
            <i className="fa-solid fa-user-large"></i> {curUserData.fullName}{" "}
            <i className="fa-solid fa-caret-down"></i>
          </li>
        )}
        {isLogin && <li onClick={logOutHandler}> ( Logout ) </li>}
      </ul>
    </nav>
  );
};
export default MainNavigation;
