import Banner from "../Components/Banner";
import ItemTypeList from "../Components/ItemTypeList";
import ItemList from "../Components/ItemList";
import { useEffect, useState } from "react";
import { useRouteLoaderData } from "react-router-dom";
import SubscribeFrom from "../Components/SubscribeForm";

const HomePage = function () {
  //ly data t route loader
  const data = useRouteLoaderData("mainpage");
  const [dataProduct, setDataProduct] = useState([]);
  //Function ly 9 sn phm u tin t data fetch c
  const getDataProduct = function () {
    setDataProduct(data.slice(0, 8));
  };
  //S dng useEffect chy hm getDataProduct khi vo page ln u
  useEffect(() => {
    getDataProduct();
  }, []);
  // component tr v JSX c to thnh t cc component con tng component s c gii thch trong file ca n
  return (
    <>
      <Banner></Banner>
      <ItemTypeList></ItemTypeList>
      <ItemList dataProduct={dataProduct}></ItemList>
      <SubscribeFrom></SubscribeFrom>
    </>
  );
};
export default HomePage;
// xy dng phn data cho loader  ly data t link api c  bi cung cp, s c s dng trong nhiu route khc nhau
export async function loader() {
  const response = await fetch(
    `${process.env.REACT_APP_BACKEND_URL}/product/getall`
  );
  return response;
}
