import NavBarProduct from "../Components/NavBarProduct";
import ProductList from "../Components/ProductList";
import styles from "./ShopPage.module.css";
import { useSelector } from "react-redux";
import { useRouteLoaderData } from "react-router-dom";
import { useState, useEffect } from "react";
const ShopPage = function () {
  //Bin lu loi category ly t redux store
  const category = useSelector((state) => state.category.category);
  //Bin ly data fetch t API c thc hin t Route loader  trang homepage vi id "mainpage"
  const data = useRouteLoaderData("mainpage");
  //To bin gi array lc ra sn phm theo category, mc nh s hin th 
  const [filterListProduct, setFilterListProduct] = useState(data);
  //Khi category thay i th filterList s c render li lc sn phm theo category
  useEffect(() => {
    if (category === "All") {
      setFilterListProduct(data);
    } else {
      setFilterListProduct(
        data.filter((product) => {
          return product.category === category.toLowerCase();
        })
      );
    }
  }, [category]);
  //component tr ra JSX theo yu cu thit k, truyn props vo product List  c fill theo category vo ProductList  hin th
  return (
    <>
      <div className={styles.title}>
        <h2>SHOP</h2>
        <h3>SHOP</h3>
      </div>
      <div className={styles.filterSection}>
        <NavBarProduct />
        <ProductList productList={filterListProduct} category={category} />
      </div>
    </>
  );
};
export default ShopPage;
