import "./App.css";
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import { Provider } from "react-redux";
import HomePage, { loader as productLoader } from "./Pages/HomePage";
import ShopPage from "./Pages/ShopPage";
import DetailPage from "./Pages/DetailPage";
import CartPage from "./Pages/CartPage";
import CheckOutPage from "./Pages/CheckOutPage";
import RootLayout from "./Pages/RootLayout";
import store from "./redux/store";
import History from "./Pages/History";
import HistoryDetail from "./Pages/HistoryDetail";
import ServerError from "./Pages/ServerError";
import LoginPage from "./Pages/LoginPage";
function App() {
  //To ra bin router  qun l router cho cc thnh phn con bn trong
  const router = createBrowserRouter([
    {
      //Root router
      path: "/",
      element: <RootLayout></RootLayout>,
      id: "mainpage",
      loader: productLoader,
      children: [
        {
          //Trang Home c cng path vi root khi dng index:true
          index: true,
          element: <HomePage></HomePage>,
        },
        {
          //Trang Shopage
          path: "/shop",
          element: <ShopPage></ShopPage>,
        },
        //Trang DetailPage hin th ng theo id sn phm
        { path: "/detail/:id", element: <DetailPage></DetailPage> },
        //Trang cart lu danh sch sn phm c thm vo gi hng
        { path: "/cart", element: <CartPage></CartPage> },
        // Trang checkout in thng tin t hng
        { path: "/checkout", element: <CheckOutPage></CheckOutPage> },
        //Trang login page v accesspage tch hp vo cng 1 trang vi action UserAccess  validate hnh ng ng nhp hoc ng k
        { path: "/login", element: <LoginPage></LoginPage> },
        { path: "/history", element: <History></History> },
        { path: "/history/:id", element: <HistoryDetail></HistoryDetail> },
        { path: "/servererror", element: <ServerError /> },
      ],
    },
  ]);
  //Functiont tr ra JSX cha Provider  truy cp vo store ca Redux v cc router
  return (
    <Provider store={store}>
      <RouterProvider router={router}></RouterProvider>
    </Provider>
  );
}

export default App;
